import React from "react";
import {Stack,Button,Text,Link,Box,Grid,GridItem,Flex,Image,extendTheme} from "@chakra-ui/react";
import mypic from "../Assets/profile.png"
function Home() {
  return (
    <Box   justifyContent='center'   w='100%' mt="150px" zIndex='-10'>
    
       <Flex flexWrap='wrap' margin='auto'   order={1} justify='center'  gap="50px" >
        <Box mt="70px">
           <Stack direction="column" gap="30px" order={1}>
            <Link >  <Image width='20px'  src="https://cdn-icons-png.flaticon.com/512/220/220343.png"></Image></Link>
             <Link > <Image width='20px'  src="https://cdn-icons-png.flaticon.com/512/1051/1051377.png"></Image></Link>
             <Link > <Image width='20px'  src="https://cdn-icons-png.flaticon.com/512/3781/3781605.png"></Image></Link>
           </Stack>
        </Box >
        <Box textAlign="left"  order={{base:"3",sm:"3",md:"2",lg:"2"}} ml={{base:'10px'}} mr={{base:"10px"}} >
           <Text fontSize='35px' mt="20px" fontWeight='bold' zIndex={0}>Hi,I'am Mohak Tandon</Text>
           <Text>Frontend developer</Text>
           <Text mt="10px">Enthusiatic in Web development and web designing</Text>
           <Button mt="30px" backgroundColor='#8A3FFC' color='white'>Contact Me</Button>
        </Box>
        <Box  h="250px"  order={{base:'2',sm:"2",md:'3',lg:'3'}}>
        
        <Image src={mypic} boxSize="250px"   backgroundColor="#8A3FFC"  borderRadius="50%"></Image>

        </Box>
       </Flex>

       </Box> 
  );
}

export default Home;